// Mock subjects for filtering (should match tutor modal)
const subjects = ["All", "Mathematics", "Physics", "Chemistry", "Biology", "History", "Literature", "Computer Science", "Other"];


export default function StudentDocumentsPage() {
  // State for documents - maybe fetch based on filters
  const [documents, setDocuments] = useState<MarketplaceDocument[]>(mockMarketplaceDocuments);
  const [searchTerm, setSearchTerm] = useState("");
  const [subjectFilter, setSubjectFilter] = useState("All");

  const handlePurchaseClick = (doc: MarketplaceDocument) => {
      console.log(`Attempting to purchase ${doc.title} for €${doc.price.toFixed(2)}`);
      // TODO: Implement payment gateway integration
      alert(`Purchase simulation: ${doc.title} for €${doc.price.toFixed(2)}. After payment, enable download.`);
      // TODO: Update document state or user's purchased list to enable download
  }

   const handleDownloadPurchased = (doc: MarketplaceDocument) => {
      console.log(`Downloading purchased document: ${doc.fileName}`);
      alert(`Download simulation (already purchased): ${doc.fileName}`);
      // TODO: Implement secure download
   }

  // Filtering logic
  const filteredDocuments = documents.filter(doc => {
      const matchesSearch = doc.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
                            doc.subject.toLowerCase().includes(searchTerm.toLowerCase()) ||
                            doc.tutor.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
                            (doc.description && doc.description.toLowerCase().includes(searchTerm.toLowerCase()));
      const matchesSubject = subjectFilter === "All" || doc.subject === subjectFilter;
      return matchesSearch && matchesSubject;
  });


  return (
    <div className="space-y-6">
      {/* Header and Filters */} 
      <div className="flex flex-col md:flex-row justify-between items-center gap-4">
        <div>
          <h1 className="text-2xl font-bold">Marketplace Documents</h1>
          <p className="text-muted-foreground">
            Browse and purchase documents uploaded by tutors.
          </p>
        </div>
        <div className="flex items-center gap-2 w-full md:w-auto">
           {/* Subject Filter */}
            <Select value={subjectFilter} onValueChange={setSubjectFilter}>
                <SelectTrigger className="w-full md:w-[180px]">
                    <SelectValue placeholder="Filter by subject..." />
                </SelectTrigger>
                <SelectContent>
                    {subjects.map((s) => (<SelectItem key={s} value={s}>{s}</SelectItem>))}
                </SelectContent>
            </Select>
           {/* Search Input */}
           <div className="relative flex-1 md:flex-initial">
             <Search className="absolute left-2 top-2.5 h-4 w-4 text-muted-foreground" />
             <Input
                placeholder="Search documents..."
                className="pl-8 w-full"
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
              />
           </div>
        </div>
      </div>

      {/* Document Grid */} 
      <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4">
        {filteredDocuments.length > 0 ? (
           filteredDocuments.map((doc) => (
             <Card key={doc.id} className="flex flex-col h-full hover:shadow-md transition-shadow">
                <CardHeader className="pb-3">
                    <CardTitle className="text-lg line-clamp-2">{doc.title}</CardTitle> 
                    <CardDescription className="flex items-center justify-between pt-1"> 
                        <span> 
                           <span className="font-medium">{doc.subject}</span>
                           <span className="mx-1.5">•</span>
                           <span>By: {doc.tutor.name}</span>
                        </span>
                        <span className="flex items-center gap-1 text-xs"> 
                           <Star className="h-3 w-3 text-yellow-400 fill-yellow-400"/> 
                           {doc.tutor.averageRating.toFixed(1)}
                        </span>
                    </CardDescription>
                </CardHeader>
                 <CardContent className="flex-1 pb-4 min-h-[60px]">
                     {doc.description && (
                        <p className="text-sm text-muted-foreground line-clamp-3">{doc.description}</p>
                     )}
                </CardContent>
                 <CardFooter className="border-t px-6 py-3 flex items-center justify-between gap-2">
                     <span className="font-semibold text-primary text-lg">
                        €{doc.price.toFixed(2)}
                     </span>
                      {/* Example: Check if purchased */}
                      {/* {doc.purchased ? (
                          <Button variant="secondary" size="sm" onClick={() => handleDownloadPurchased(doc)}>
                             <Download className="mr-2 h-4 w-4"/> Download
                          </Button>
                      ) : ( */}
                          <Button size="sm" onClick={() => handlePurchaseClick(doc)}>
                             <ShoppingCart className="mr-2 h-4 w-4"/> Buy Now
                          </Button>
                      {/* )} */}
                 </CardFooter>
             </Card>
           ))
        ) : (
           <p className="text-center text-muted-foreground py-8 md:col-span-2 lg:col-span-3 xl:col-span-4">
               No documents found matching your criteria.
           </p>
        )}
      </div>

       {/* Optional: Could add a separate section/page for "My Purchased Documents" */}
    </div>
  );
} 